<script>
export default {

  name: 'Register',
  data() {
    return {
      picList: [],
      bg_url:'',
      //ç»‘å®šç™»å½•ç•Œé¢çš„inputå€¼
      log_username: '',
      log_password: '',
      //ç»‘å®šäº†æ³¨å†Œç•Œé¢çš„inputå€¼
      register_nickname: '',
      register_username: '',
      register_password: '',
      register_verifycode: '',
      //æ˜¯å¦æ˜¯ç™»å½•é¡µé¢ false ä¸ºæ³¨å†Œé¡µé¢
      isLog: false,
      //ç™»å½•ç•Œé¢,æ˜¯å¦è®°ä½ç”¨æˆ·åå¯†ç é€‰é¡¹ falseä¸ºæœªæ‰“é’©
      remember: false,
      //ç™»å½•ç•Œé¢ä¸‹æ–¹ç¤¾äº¤è´¦å·ç™»å½•icon
      login_icon_hrefs: ['#icon-ziyuan', '#icon-shejiao-instagram', '#icon-shejiao-github', '#icon-shejiao1', '#icon-shejiao-facebook'],
      //æ³¨å†Œç•Œé¢ä¸‹æ–¹ç¤¾äº¤è´¦å·æ³¨å†Œicon
      register_icon_hrefs: ['#icon-ziyuan', '#icon-weibo', '#icon-shejiao-twitter', '#icon-shejiao1', '#icon-shejiao-github'],
      //è¡¨ç¤ºæ³¨å†Œç•Œé¢æ‰‹æœºå·æ ¼å¼é”™è¯¯messageå¼¹çª—æ˜¯å¦æ˜¯æ¿€æ´»çŠ¶æ€ è‹¥æ˜¯æ¿€æ´»çŠ¶æ€åˆ™ä¸ä¼šäº§ç”ŸäºŒæ¬¡å¼¹çª—
      user_msg_showing: false,
      //è¡¨ç¤ºè¾“å…¥æ‰‹æœºéªŒè¯ç çš„æ¡†æ˜¾ä¸æ˜¾ç¤º
      verify_code_show: false,
      hostname:'47.94.129.112',
    }
  },

  mounted: function() {
    const t = this
    fetch(`http://${t.hostname}/api/get_pic_urls`, {
        method: 'get',
      })
      .then(re => re.json())
      .then(re => {
        for(let i of re.list){
          t.picList.push({
            pic_name:i.fields.pic_name,
            pic_url:i.fields.pic_url
          })
          if(i.fields.pic_name == 'background'){
            t.bg_url = i.fields.pic_url
          }
        }
      })
  },

  methods: {
    //ç‚¹å‡»ç™»å½•
    setLog: function() {
      if (!this.isLog) {
        this.setNull();
        this.isLog = true;
        this.verify_code_show = false;

        return;
      }
    },
    //ç‚¹å‡»æ³¨å†Œ
    setReg: function() {
      if (this.isLog) {
        this.setNull();
        this.isLog = false;
        return;
      }
    },
    //æ›´æ¢ç™»å½•æ³¨å†ŒçŠ¶æ€å°†æ‰€æœ‰inputæ¡†å€¼æ¸…ç©º
    setNull: function() {
      this.log_username = '';
      this.log_password = '';
      this.register_nickname = '';
      this.register_username = '';
      this.register_password = '';
      this.register_verifycode = '';
    },

    _login: function() {
      const t = this
      if(t.log_username.length!=11){
        this.$message.error('æ‚¨çš„æ‰‹æœºå·è¾“å…¥æœ‰è¯¯ï¼Œè¯·é‡æ–°è¾“å…¥');
        t.log_username = ''
        return
      }
      fetch(`http://${t.hostname}/api/account_login`, {
          method: 'post',
          body: 'username=' + t.log_username + '&password=' + t.log_password,
          headers: {
            "Accept": "application/json",
            "Content-Type": "application/x-www-form-urlencoded"
          },
        })
        .then(re => re.json())
        .then(re => {
          if(re.msg == 'success'){
            this.$message({
              message: 'ç™»å½•æˆåŠŸï¼',
              type: 'success'
            });

            setTimeout(function () {
                t.$router.push({ path:'/main' })
            },2500)

            return
          }else if (re.msg =='passwordError') {
            this.$message.error('è´¦å·å¯†ç æœ‰è¯¯ï¼Œè¯·é‡æ–°è¾“å…¥');
            t.log_password = ''
            return
          }else if (re.msg == 'notRegisterd') {
            this.$message({
              message: 'æ­¤ç”¨æˆ·åæœªæ³¨å†Œï¼Œè¯·å…ˆæ³¨å†Œ',
              type: 'warning'
            });
            t.setReg();
            return
          }
        })
    },
    _verify:function () {
      const  t = this
      if(t.register_username.length!=11){
        if(t.register_username.length == 0){
          return
        }
        this.$message.error('æ‚¨çš„æ‰‹æœºå·è¾“å…¥æœ‰è¯¯ï¼Œè¯·é‡æ–°è¾“å…¥');
        t.register_username = ''
        return
      }
      $(".main-part").css("height", "600px");
      $("#after-move").css("transform", "translateY(25px)");
      t.verify_code_show = true
    },
    _getVerifyCode: function() {
      const t = this
      if (t.register_username.length != 11) {
        this.$message.error('æ‚¨çš„æ‰‹æœºå·è¾“å…¥æœ‰è¯¯ï¼Œè¯·é‡æ–°è¾“å…¥');
        t.register_username = ''
        return
      }
      setTimeout(function() {
        t.register_verifycode = 123456
      }, 3000)
    },
    _register: function() {
      const t = this
      if (t.register_nickname.length < 7) {
        this.$message.error('æ‚¨è¾“å…¥çš„æ˜µç§°è¿‡çŸ­ï¼Œé•¿åº¦åº”å¤§äº6');
        return
      }
      if (t.register_username.length != 11) {
        this.$message.error('æ‚¨çš„æ‰‹æœºå·è¾“å…¥æœ‰è¯¯ï¼Œè¯·é‡æ–°è¾“å…¥');
        return
      }
      fetch(`http://${t.hostname}/api/account_register`, {
          method: 'post',
          body: 'username=' + t.register_username + '&password=' + t.register_password + '&nickname=' + t.register_nickname,
          headers: {
            "Accept": "application/json",
            "Content-Type": "application/x-www-form-urlencoded"
          },
        })
        .then(re => re.json())
        .then(re => {
          if (re.msg == 'success') {
            this.$message({
              message: 'æ­å–œæ‚¨ï¼Œæ³¨å†ŒæˆåŠŸï¼',
              type: 'success'
            });
          } else if (re.msg == 'registerd username') {
            this.$message({
              message: 'è¯¥ç”¨æˆ·åå·²è¢«æ³¨å†Œ',
              type: 'warning'
            });
          }
        })
    },
  },
}
</script>

